<template>
  <div class="body" id="app">
    <div style="margin-left:200px">
      <h1 >Z0-Team</h1>
      <label>From internet ,For internet.
      </label>
    </div>

    <div style="float:left;height:800px" >
      <div>
        <h1>Me</h1>
        <label>
        于小葵
        <br>
        信息安全爱好者，喜欢研究技术和教人。
        <br>Twitter： <a href="https://twitter.com/Xi4okui">@Xi4okui</a>  || <a href="https://github.com/yuxiaokui">Gayhub</a></label>
      </div>

      <div>
        <h1>Projects</h1>
        <label><a href="https://github.com/yuxiaokui/gohtran">Gohtran</a>
        <br>
        反向Socks5代理。可以随处使用内网肉鸡的网络。
        </label>
        <br>
        <br>
        <label><a href="https://github.com/yuxiaokui/cloudhacker">Cloudhacker</a>
        <br>
        从zoomeye or shodan or file 获取目标进行攻击。
        </label>
        <br>
        <br>
        <label><a href="https://github.com/yuxiaokui/xkscan">xkscan</a>
        <br>
        C段端口扫描，redis，mongodb,memcache,elasticsearch，S2-045漏洞，headers探测，phpinfo文件探测。
        </label>
        <br>
        <br>
        <label><a href="https://github.com/yuxiaokui/xicidaili_tools">xicidaili_tools</a>
        <br>
        自动获取xicidaili列表，并验证可用。
        </label>
      </div>

      <div>
        <h1>Blog</h1>
        <label><a href="https://github.com/yuxiaokui/onethink_rce">onethink_thinkphp_3.2_rce</a>
        <br>
        Onethink CMS 基于thinkphp 3.2版本的SQL注入漏洞 and 后台getshell。
        </label>
        <br>
        <br>
        <label><a href="https://github.com/yuxiaokui/thinkcmf_rce">thinkcmf_rce</a>
        <br>
        thinkcmf_rce
        </label>
        <br>
        <br>
        <label><a href="https://zhuanlan.zhihu.com/joke-hacker">黑客娱乐圈</a>
        <br>
        我的逼呼专栏，分享一些安全娱乐圈的新闻。
        </label>
      </div>


    </div>

    <div style="float:righ; margin-left:400px">
      <h1>黑客江湖聊天室</h1>
      大侠贵姓：<input type="text" v-model="name"><br>
      大侠请留言：<input type="text" v-model="text" @keyup.enter="send()">
      <br>

      <el-button type="info" icon="el-icon-message" @click="send()" circle></el-button>
      <el-button type="danger" icon="el-icon-delete" @click="clear()" circle></el-button>
      <br>
      <br>
      <div v-html="data"></div>
      当前在线人数：{{online_num}}
      <br>
      ----------------------------------
      <div v-html="result" ></div>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
         name: '',
         text: '',
         data: '',
         online_num: 0,
         result: '',
         websocket: null,
         history: ''
     };
  },

  mounted() {
     var string = "段誉,虚竹,阿紫,乔峰,阿朱,慕容复,王语嫣,段正淳,木婉清,鸠摩智,游坦之,丁春秋,钟灵,包不同,马夫人,乌老大,阿碧,段延庆,玄难,玄慈,风波恶,钟万仇,耶律洪基,云中鹤,巴天石,叶二娘,苏星河,李秋水,全冠清";
     var array = string.split(",");
     this.name = array[Math.round(Math.random()*(array.length-1))];
     if ('WebSocket' in window) {
       this.websocket = new WebSocket('wss://home.z0.hk:3000/chat')
       this.initWebSocket()
     } else {
       alert('当前浏览器 Not support websocket')
     }
   },
 beforeDestroy() {
   this.onbeforeunload()
 },
 methods: {
   initWebSocket() {
     //连接错误
     this.websocket.onerror = this.setErrorMessage
     // //连接成功
     this.websocket.onopen = this.setOnopenMessage
     //收到消息的回调
     this.websocket.onmessage = this.setOnmessageMessage
     //连接关闭的回调
     this.websocket.onclose = this.setOncloseMessage
     //监听窗口关闭事件，当窗口关闭时，主动去关闭websocket连接，防止连接还没断开就关闭窗口，server端会抛异常。
     window.onbeforeunload = this.onbeforeunload
   },
   setErrorMessage() {
     this.data = "聊天室连接发生错误" + '   状态码：' + this.websocket.readyState;
   },
   setOnopenMessage() {
     this.data = "聊天室连接成功" + '   状态码：' + this.websocket.readyState;
   },
   setOnmessageMessage(event) {
     var data = JSON.parse(event.data);
     console.log(data)
     this.online_num = data.online_num
     if (data.message != "")
     {
       if (this.history != data.message)
       { var temp
         temp = data.message.replace("<", "&lt;")
         this.result += temp
         this.result += "<br>";
       }
       this.history = data.message
     }
   },
   setOncloseMessage() {
     this.data = "聊天室连接关闭" + '   状态码：' + this.websocket.readyState;
   },
   onbeforeunload() {
     this.closeWebSocket();
   },
   //websocket发送消息
   send() {
     if(this.text == ''){
       alert('说点啥吧！')
     }
     else{
       this.websocket.send(this.name + ":" + this.text)
       this.text = ''
     }
   },
   clear() {
       this.result = ''
   },
   closeWebSocket() {
     this.websocket.close()
   }
 }
}
</script>


<style>
body {
  background: #20201d;
  color: #a6a28c;
  margin-left: 20%;
}
a{
  color: #e8e4cf;
}
</style>



