import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import App from './App.vue'
import VueAnalytics from 'vue-analytics'

Vue.use(ElementUI)
Vue.use(VueAnalytics, {
  id: 'UA-150114359-1',
  autoTracking: {
    pageviewOnLoad: false
  }
})
new Vue({
  el: '#app',
  render: h => h(App)
})



